<template>
  <div id="footer-bar" class="animated fadeIn">
    <mt-tabbar class="zdcj-tabbar" v-model="selectedTab" fixed>
      <mt-tab-item id="" href="#/newslive">
        <img slot="icon" src="../assets/nav-news.svg">
        
      </mt-tab-item>
      <mt-tab-item id="" href="#/markets">
        <img slot="icon" src="../assets/nav-markets.svg">
        
      </mt-tab-item>
      <mt-tab-item id="" href="#/calendar">
        <img slot="icon" src="../assets/nav-calendar.svg">
        
      </mt-tab-item>
      <mt-tab-item id="" href="#/user">
        <img slot="icon" src="../assets/nav-user.svg">
        
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>

export default {
  data () {
    return {
      selectedTab: ''
    }
  },

  methods: {
    checkCurPage(route){
      switch(route.name)
      {
        case 'userPage':
          this.selectedTab = '';
          break;
        case 'kuaixun':
          this.selectedTab = '';
          break;
        case 'zdtt':
          this.selectedTab = '';
          break;
        case 'hangqing':
          this.selectedTab = '';
          break;
        case 'rili':
          this.selectedTab = '';
          break;
      }
    }
  },

  watch: {
    '$route' (to, from) {
      this.checkCurPage(to);
    }
  },

  created(){
    this.checkCurPage(this.$route);
  }
}
</script>

<style lang="scss">
</style>


